@import url('https://fonts.googleapis.com/css2?family=Grand+Hotel&display=swap');

.brand-logo,h2{
  font-family: 'Grand Hotel', cursive;
}

a{
  color: black !important;
}
.mycard{
  margin-top: 30px;
}
.auth-card{
  padding: 20px;
  text-align: center;
  max-width: 400px;
  margin: 10px auto;
}
.signin{
  margin: 10px 0 0;
}
.input-field input[type=text],[type=password]:focus {
  border-bottom: 1px solid rgb(8, 93, 252) !important;
  box-shadow: 0 1px 0 0 rgb(8, 93, 252) !important;
}
.check-btn span{
  color:  #64B5F6;
}
.gallery{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.item{
  width: 30%;
}

.home-card{
  max-width: 500px;
  height: max-content;
  margin: 26px auto;
}

#toast-container{
  top:60px !important;
  right: 20px !important;
  left: auto !important;
}
.material-icons:hover{
  cursor: pointer;
}